*{
  margin: 0;
}

.App {
  text-align: center;
  font-family: 'Acme', sans-serif;
}   
.homepage{
    display: flex;
    flex-direction: column;
    width: 50vw;
    height: 70vh;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px gray;
    background: white;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
}
.homepage >ul> li> .button{
    background: #1877f2;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}
.homepage >ul > li > .download{
    font-size: 1.25rem;
    padding: 0.5rem 0;
    outline: none;
    cursor: pointer;
}.homepage >ul> li> a{
    text-decoration: none;
}

@media (max-width: 768px){
    .homepage{
        display: flex;
        flex-direction:  column;
        width: 90vw;
        height: 70vh;
        margin-left: auto;
        margin-right: auto;
        margin-top: 22%;
    }
}
@media (min-width: 769px) and (max-width: 1023px){
    .homepage{
        display: flex;
        flex-direction:  column;
        width: 90vw;
        height: 70vh;
        margin-left: auto;
        margin-right: auto;
        
    }
}
.login{
    width: 400px;
    background: #fff;
    border: 1px solid #dddfe2;
    box-shadow: 0 2px 4px rgb(0 0 0 /10%), 0 8px 16px rgb(0 0 0 /10%);
    border-radius: 8px;
    padding: 1rem;
    align-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10%;
}
.login > input{
    border-radius: 8px;
    border: 2px solid #dddfe2;
    outline: none;
    margin: 0.5rem 0;
    padding: 0.5rem 0.75rem;
    width: 99%;
    font-size: 1rem;
}
.login > .button{
    background: #1877f2;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}

@media (max-width: 768px){
    .login{
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        margin-top: 25%;
    }
}

@media (min-width: 769px) and (max-width: 1023px){
    .login{
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        margin-top: 25%;
    }
}
